import "./styles.css";
import { isSupported, setup } from "@loomhq/loom-sdk";

const BUTTON_ID = "UNIQUE_BUTTON_ID";

async function init() {
  const root = document.getElementById("app");

  root.innerHTML = `
    <div className="playground">
      <h1>@loomhq/loom-sdk playground</h1>
      <div className="demo-button">
        <button id="${BUTTON_ID}">Record</button>
      </div>
    </div>
`;

  const button = root.querySelector(`#${BUTTON_ID}`);

  if (button == null || !isSupported()) {
    return;
  }

  const { configureButton } = await setup({
    apiKey: "c3e7c699-2db0-4871-bf13-eee834238e9b"
  });

  configureButton({
    element: button,
    hooks: {
      onInsertClicked: (shareLink) => {
        console.log("clicked insert");
        console.log(shareLink);
      },
      onStart: () => console.log("start"),
      onCancel: () => console.log("cancelled"),
      onComplete: () => console.log("complete")
    }
  });
}

init();
